<template>

	<view class="body">
		<view class="head">
			<view class="head_back" >
				<image src="@/static/img/back.png" mode="" @click="navigateBack()"></image>
			</view>
			<view class="head_center hei_38_bold">{{title}}</view>
			<view class=" head_right hei_30_bold" >
				<text @click="add">新增</text>
			</view>
		</view>

		<view class="zi_body ">
			<!-- 社会职务 -->
			<view class="list" v-if="title=='社会职务'" v-for="(item,index) in 5" @click="zhiwu_xq()">
				<view class="list_left">
					<view class="hei_26 time">
						{{renqi}}
					</view>
					<view class="hei_28">
						杭州律师事务所 律师
					</view>
				</view>
				<image src="@/static/lsimg/go_r.png" mode=""></image>
			</view>
			<!-- 荣誉奖励 -->
			<view class="list" v-if="title=='荣誉奖励'" v-for="(item,index) in 5" @click="rongyu_xq()">
				<view class="list_left">
					<view class="hei_26 time">
						{{time}}
					</view>
					<view class="hei_28">
						杭州市西湖区人民政府颁发的'西湖区优秀社区律师'
					</view>
				</view>
				<image src="@/static/lsimg/go_r.png" mode=""></image>
			</view>
			<!-- 媒体报道 -->
			<view class="list" v-if="title=='媒体报道'" v-for="(item,index) in 5" @click="meiti_xq()">
				<view class="list_left">
					<view class="hei_26 time">
						{{time}}
					</view>
					<view class="hei_28">
						报道的内容
					</view>
				</view>
				<image src="@/static/lsimg/go_r.png" mode=""></image>
			</view>
			<!-- 学术成果 -->
			<view class="list" v-if="title=='学术成果'" v-for="(item,index) in 5" @click="xueshu_xq()">
				<view class="list_left">
					<view class="hei_26 time">
						{{time}}
					</view>
					<view class="hei_28">
						获得劳动成果
					</view>
				</view>
				<image src="@/static/lsimg/go_r.png" mode=""></image>
			</view>



		</view>

	</view>



</template>

<script>
	export default {

		data() {
			return {
				title: '',
				renqi: '曾任',
				time: '2020-12-12',
				youxiang: '',
				weixin: '',
				dizhi: ''
			}
		},
		created() {

		},
		onLoad(option) {

			this.title = option.title
		},
		methods: {
			navigateBack() {
				uni.navigateBack()
			},

			add() {

				if (this.title == '社会职务') {
					console.log('4444')
					uni.navigateTo({
						url: 'ls_zhiwu'
					})
				}else if(this.title=='荣誉奖励'){
				uni.navigateTo({
					url: 'ls_rongyu'
				})
				}else if(this.title=='媒体报道'){
				uni.navigateTo({
					url: 'ls_meiti'
				})
				}else if(this.title=='学术成果'){
					uni.navigateTo({
						url: 'ls_xueshu'
					})
				}


			},
			zhiwu_xq(){
				uni.navigateTo({
					url:'ls_zhiwu'
				})
			},
			rongyu_xq(){
				uni.navigateTo({
					url:'ls_rongyu'
				})
			},
			meiti_xq(){
				uni.navigateTo({
					url:'ls_meiti'
				})
			},
			xueshu_xq(){
				uni.navigateTo({
					url:'ls_xueshu'
				})
			},
			

		}
	}
</script>

<style>
	page{
		background-color: #F8F8F8;
	}
	.head {
		border-bottom: 2rpx solid #E8E8E8;
	}

	.list {
		padding: 20rpx 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20rpx;
		padding-left: 30rpx;
		padding-right: 30rpx;
		position: relative;
		background-color: #FFFFFF;
	}

	.list_left {
		max-width: 95%;
	}

	.list image {
		width: 13rpx;
		height: 23rpx;
	}
	.time{
		margin-bottom: 10rpx;
	}
</style>
